import * as React from 'react'

import { Grid, IconButton } from '@material-ui/core'
import { Check, Close } from '@material-ui/icons'
import { GetTransactions_transactions_prediction as IPrediction } from '@src/queries/__generated__/GetTransactions.hooks'

interface SmartCatProp {
  prediction: IPrediction
  onAccept: () => void
}
export const SmartCatField: React.FC<SmartCatProp> = (props) => {
  const { prediction, onAccept } = props
  const split = prediction.split ? 'Split ' : ''
  return (
    <div style={{ color: 'rgb(0, 188, 212)', fontWeight: 'bold' }}>
      {`${split}${prediction.categoryName}?`}
      <Grid container>
        <Grid item xs={12} sm={1}>
          <IconButton onClick={() => onAccept()}>
            <Check color="primary" />
          </IconButton>
        </Grid>
        <Grid item xs={12} sm={1}>
          <IconButton
            onClick={() => {
              // Do nothing because the Transaction row will be focused and enter edit mode
            }}
          >
            <Close color="secondary" />
          </IconButton>
        </Grid>
      </Grid>
    </div>
  )
}
