import {
  ExpansionPanel,
  ExpansionPanelDetails,
  ExpansionPanelSummary,
  Fab,
  Grid,
  Icon,
  Tooltip,
  Typography,
} from '@material-ui/core'
import { Delete } from '@material-ui/icons'
import { useMutation } from '@apollo/react-hooks'
import * as _ from 'lodash'
import JSONTree from 'react-json-tree'
import * as React from 'react'
import { SmartCatField } from './SmartCatField'
import { CategoryField } from './CategoryField'
import {
  QUERY as MUTATION,
  UpdateTransaction,
  UpdateTransactionVariables,
} from '@src/queries/__generated__/UpdateTransaction.hooks'
import {
  QUERY as GET_TRANSACTIONS_QUERY,
  GetTransactions,
  GetTransactions_categories,
  GetTransactions_transactions as GetTransactions_transaction,
} from '@src/queries/__generated__/GetTransactions.hooks'
import { DebouncedTextField } from '@src/components/DebouncedTextField'
import { formatDate } from '@src/utils'

import {
  QUERY as ARCHIVE_TRANSACTION_MUTATION,
  ArchiveTransaction,
  ArchiveTransactionVariables,
} from '@src/queries/__generated__/ArchiveTransaction.hooks'
import { PlaidIcon } from '@src/components/icons'

interface ITransactionRowProps {
  transaction: GetTransactions_transaction
  user_id: number
  expanded: boolean
  selectableCategories: Array<GetTransactions_categories>
  onToggleExpansion: (event, expanded) => void
}

export const CategoryInfoField = (props: {
  transaction: GetTransactions_transaction
  category: GetTransactions_categories | null
  user_id: number
  onAccept: (arg: { id: number; category_id: number; balance_adjust: string }) => void
}) => {
  const { transaction, category, onAccept } = props
  const prediction = transaction.prediction
  const multiplier = props.user_id === transaction.lenderId ? 1 : -1
  if (!transaction.categoryId && prediction) {
    return (
      <SmartCatField
        prediction={prediction}
        onAccept={() =>
          onAccept({
            id: transaction.id,
            category_id: prediction.categoryId,
            balance_adjust: prediction.split
              ? ((multiplier * parseFloat(transaction.totalAmount)) / 2).toString()
              : '0',
          })
        }
      />
    )
  }
  return (
    <Grid container spacing={10}>
      <Grid item xs={1}>
        <Icon className="material-icons">{category ? category.mui_icon_id : null}</Icon>
      </Grid>
      <Grid item xs>
        {category ? <Typography color="textSecondary">{category.description}</Typography> : null}
      </Grid>
    </Grid>
  )
}

export const TransactionRowComponent = (props: ITransactionRowProps) => {
  const { user_id, selectableCategories, transaction, expanded, onToggleExpansion } = props
  const [balanceAdjust, setBalanceAdjust] = React.useState(transaction.balanceAdjust)
  const [notes, setNotes] = React.useState(transaction.notes)
  console.log('render row')

  const [updateTransaction, { loading }] = useMutation<
    UpdateTransaction,
    UpdateTransactionVariables
  >(MUTATION)
  const [archiveTransaction] = useMutation<ArchiveTransaction, ArchiveTransactionVariables>(
    ARCHIVE_TRANSACTION_MUTATION,
    {
      update(cache, { data }) {
        if (data?.archiveTransaction?.isOk) {
          const cached = cache.readQuery<GetTransactions>({ query: GET_TRANSACTIONS_QUERY })
          const removed = [...cached!.transactions]
          _.remove(removed, (t) => t.id === transaction.id)
          cache.writeQuery({
            query: GET_TRANSACTIONS_QUERY,
            data: Object.assign({}, cached, { transactions: removed }),
          })
        }
      },
    },
  )

  const onAcceptSmartCat = (fields: {
    id: number
    category_id: number
    balance_adjust: string
  }) => {
    updateTransaction({
      variables: {
        id: transaction.id,
        categoryId: fields.category_id,
        balanceAdjust: fields.balance_adjust,
      },
    })
  }
  const getCategory = () => {
    if (!transaction.categoryId) return null
    return selectableCategories.find((c) => c.id === transaction.categoryId) || null
  }
  return (
    <div>
      <ExpansionPanel
        disabled={!transaction.editable}
        expanded={expanded}
        onChange={onToggleExpansion}
      >
        <ExpansionPanelSummary>
          <Grid container spacing={10}>
            <Grid item xs={3} sm={4}>
              <Typography>{formatDate(transaction.issuedDate)}</Typography>
              {expanded ? null : (
                <CategoryInfoField
                  user_id={user_id}
                  transaction={transaction}
                  category={getCategory()}
                  onAccept={onAcceptSmartCat}
                />
              )}
            </Grid>
            <Grid item xs={6} sm={4}>
              {transaction.originPayload ? (
                <Tooltip
                  title={
                    <JSONTree shouldExpandNode={() => true} data={transaction.originPayload} />
                  }
                >
                  {transaction.originPayload.__typename === 'IWaveTransaction' ? (
                    <Icon style={{ color: 'blue', float: 'left' }} className="material-icons">
                      waves
                    </Icon>
                  ) : (
                    <PlaidIcon />
                  )}
                </Tooltip>
              ) : null}
              <Typography variant="body1">{transaction.merchantName}</Typography>
              <div>
                <Typography color="textSecondary">{transaction.accountName}</Typography>
                <Typography color="textSecondary">{transaction.plaidCategory}</Typography>
                <Typography color="textSecondary">{transaction.notes}</Typography>
              </div>
            </Grid>
            <Grid item xs={3} sm={4} style={{ paddingLeft: '40px' }}>
              <Typography>{`$${transaction.totalAmount}`}</Typography>
              {+transaction.balanceAdjust && !expanded ? (
                <Typography
                  style={{
                    color: +transaction.balanceAdjust > 0 ? 'green' : 'red',
                  }}
                >
                  {`$${transaction.balanceAdjust}`}
                </Typography>
              ) : null}
            </Grid>
          </Grid>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Grid container spacing={10}>
            <Grid item xs={6} sm={4}>
              <DebouncedTextField
                loading={loading}
                helperText={'notes'}
                value={notes}
                setter={(newValue) => {
                  setNotes(newValue)
                }}
                onBounce={(newValue) =>
                  updateTransaction({
                    variables: { id: transaction.id, notes: newValue },
                  })
                }
              />
            </Grid>
            <Grid item xs={6} sm={4}>
              <CategoryField
                transaction={transaction}
                selectableCategories={selectableCategories}
              />
            </Grid>
            <Grid item xs={6} sm={3}>
              <DebouncedTextField
                type="number"
                loading={loading}
                helperText="Balance Adjust"
                value={balanceAdjust}
                setter={setBalanceAdjust}
                onBounce={(newValue) => {
                  updateTransaction({
                    variables: { id: transaction.id, balanceAdjust: newValue },
                  })
                }}
              />
            </Grid>
            <Grid item xs={6} sm={1} style={{ textAlign: 'right' }}>
              <Fab
                size="medium"
                color="secondary"
                aria-label="Delete"
                onClick={() =>
                  archiveTransaction({
                    variables: {
                      id: transaction.id,
                    },
                  })
                }
              >
                <Delete />
              </Fab>
            </Grid>
          </Grid>
        </ExpansionPanelDetails>
      </ExpansionPanel>
    </div>
  )
}
const areEqual = (last: ITransactionRowProps, next: ITransactionRowProps) => {
  if (
    _.isEqual(last.transaction, next.transaction) &&
    last.expanded === next.expanded &&
    last.selectableCategories.length === next.selectableCategories.length
  ) {
    return true
  }
  return false
}
const TransactionRow = React.memo(TransactionRowComponent, areEqual)

/*
 * This is a wrapper component that is used to control the expansion states of
 * the rows. We do not want a compolicated component to manage the expansion
 * state becaue the rerending will be too expensive.
 */
export const TransactionRows: React.FC<{
  categories: Array<GetTransactions_categories>
  transactions: Array<GetTransactions_transaction>
}> = (props) => {
  const [expanded, setExpanded] = React.useState<number | null>(null)
  const handleChange = (eventId: number) => (event: React.ChangeEvent<{}>, isExpanded: boolean) => {
    setExpanded(eventId)
  }
  return (
    <>
      {props.transactions.map((t) => {
        return (
          <TransactionRow
            user_id={1}
            selectableCategories={props.categories}
            expanded={expanded === t.id}
            key={t.id}
            transaction={t}
            onToggleExpansion={handleChange(t.id)}
          />
        )
      })}
    </>
  )
}
