import { InMemoryCache, IntrospectionFragmentMatcher } from 'apollo-cache-inmemory'
import { setContext } from 'apollo-link-context'

import { ApolloClient as AC } from 'apollo-client'
import { concat } from 'apollo-link'
import { onError } from 'apollo-link-error'
import { HttpLink } from 'apollo-link-http'
import { BASE_URL } from '@src/actions'

const fragmentMatcher = new IntrospectionFragmentMatcher({
  introspectionQueryResultData: {
    __schema: {
      types: [],
    },
  },
})

export const errorLink = onError(({ graphQLErrors, networkError }) => {
  // NO-op for now
  if (graphQLErrors)
    graphQLErrors.forEach((gqlError) => {
      if (gqlError.extensions && gqlError.extensions.code == '401') {
        window.location.href = '/LoginPage'
        console.error('Authenticated')
      }
    })

  if (networkError) console.error(`[Network error]: ${networkError}`)
})

const getLink = () => {
  return concat(authLink, errorLink).concat(
    new HttpLink({
      uri: `${BASE_URL}/RAHoliCy/graphql`,
      headers: {
        authorization: `Bearer ${sessionStorage.getItem('authToken')}`,
      },
    }),
  )
}
const authLink = setContext((_, { headers }) => {
  const token = sessionStorage.getItem('authToken')
  return {
    headers: {
      ...headers,
      authorization: token ? `Bearer ${token}` : '',
    },
  }
})

export const client = new AC({
  link: getLink(),
  cache: new InMemoryCache({
    fragmentMatcher,
  }),
})
