import {
  Button,
  Divider,
  ExpansionPanel,
  ExpansionPanelActions,
  ExpansionPanelSummary,
  Grid,
  TextField,
} from '@material-ui/core'
import * as React from 'react'
import { DialogSelect } from '@src/components/DialogSelect'
import { useApolloQuery } from '@src/queries/__generated__/CreateTransaction.hooks'
import { CreateTransaction__Request } from '@src/types'
import { GetTransactions_categories as ICategory } from '@src/queries/__generated__/GetTransactions.hooks'
import { getToday } from '@src/utils'

interface NewTransactionFormProps {
  onClose: () => void
  selectableCategories: Array<ICategory>
}

type FieldName =
  | 'issued_date'
  | 'merchant'
  | 'notes'
  | 'category_id'
  | 'total_amount'
  | 'balance_adjust'

const fieldConfig = {
  issued_date: { type: 'date', required: true },
  merchant: { type: 'text', required: true },
  notes: { type: 'text', required: false },
  category_id: { type: 'number', required: false },
  balance_adjust: { type: 'number', required: false },
  total_amount: { type: 'number', required: true },
}

const FieldInput = (props: {
  fieldName: FieldName
  updateField: (fieldName) => (event) => void
  instance: CreateTransaction__Request
}) => {
  const { fieldName, updateField, instance } = props
  const config = fieldConfig[fieldName]
  let error = false
  if (config.required && !instance[fieldName]) {
    error = true
  } else if (config.required && config.type === 'number' && !instance[fieldName]) {
    error = true
  }

  return (
    <TextField
      type={config.type}
      required={config.required}
      error={error}
      label={fieldName}
      value={instance[fieldName]}
      onChange={updateField(fieldName)}
    />
  )
}
const initialForm = {
  issued_date: getToday(),
  merchant: '',
  notes: '',
  category_id: null,
  total_amount: '0',
  balance_adjust: '0',
}

export const NewTransactionForm: React.FC<NewTransactionFormProps> = (props) => {
  const { selectableCategories, onClose } = props
  const [formData, setFormData] = React.useState<CreateTransaction__Request>(initialForm)
  const [createTransaction, { loading }] = useApolloQuery({})

  const createNewTransaction = () => {
    createTransaction({
      variables: {
        input: {
          issuedDate: formData.issued_date,
          totalAmount: formData.total_amount,
          merchantName: formData.merchant,
          balanceAdjust: formData.balance_adjust || '0',
          categoryId: formData.category_id,
          notes: formData.notes,
        },
      },
    })
  }
  const updateField = (fieldName: FieldName) => {
    const old = formData
    return (event) => {
      const newState = Object.assign({}, old, {
        [fieldName]: event.target.value,
      })
      setFormData(newState)
    }
  }
  const isValid = () => {
    const t = formData
    return [t.issued_date, t.merchant, +t.total_amount].every(Boolean)
  }
  const transaction = formData
  return (
    <div style={{ paddingTop: '10px' }}>
      <ExpansionPanel expanded={true}>
        <ExpansionPanelSummary>
          <Grid container spacing={10}>
            <Grid item xs={6} sm={4}>
              <FieldInput
                instance={transaction}
                fieldName="issued_date"
                updateField={updateField}
              />
            </Grid>
            <Grid item xs={6} sm={4}>
              <FieldInput fieldName="merchant" instance={transaction} updateField={updateField} />
            </Grid>
            <Grid item xs={6} sm={4}>
              <FieldInput
                fieldName="total_amount"
                instance={transaction}
                updateField={updateField}
              />
            </Grid>
            <Grid item xs={6} sm={4}>
              <DialogSelect
                value={transaction.category_id || ''}
                onSelectOption={(option) => {
                  const newState = Object.assign({}, formData, {
                    category_id: option.value,
                  })
                  setFormData(newState)
                }}
                choices={selectableCategories.map((cat) => {
                  return {
                    value: cat.id,
                    label: cat.description,
                    mui_icon_id: cat.mui_icon_id || '',
                  }
                })}
              />
            </Grid>
            <Grid item xs={6} sm={4}>
              <FieldInput fieldName="notes" instance={transaction} updateField={updateField} />
            </Grid>
            <Grid item xs={6} sm={4}>
              <FieldInput
                fieldName="balance_adjust"
                instance={transaction}
                updateField={updateField}
              />
            </Grid>
          </Grid>
        </ExpansionPanelSummary>
        <Divider />
        <ExpansionPanelActions>
          <Button
            size="small"
            onClick={() => {
              setFormData(initialForm)
              onClose()
            }}
          >
            Cancel
          </Button>
          <Button size="small" color="primary" onClick={createNewTransaction} disabled={!isValid()}>
            {loading ? `saving ...` : `Save`}
          </Button>
        </ExpansionPanelActions>
      </ExpansionPanel>
    </div>
  )
}
