import {
  Select,
  Button,
  Divider,
  ExpansionPanel,
  ExpansionPanelActions,
  ExpansionPanelSummary,
  Grid,
  MenuItem,
} from '@material-ui/core'

import * as React from 'react'
import { FileUploadButton } from '../components/FileUploadButton'
import { EnumStatementSource } from '@src/__generated__/backendSchemas'

interface OwnProps {
  onClose: () => void
}

interface State {
  selectedSource: EnumStatementSource
}

export class NewStatementForm extends React.Component<OwnProps, State> {
  state = {
    selectedSource: EnumStatementSource.CHEQUE,
  }

  onSelectType = (event) => {
    this.setState({ selectedSource: event.target.value })
  }

  submitFileContent = (content: string) => {
    if (this.state.selectedSource.length && content) {
      console.error('doing nothing')
    }
  }

  render() {
    const choices = ['CHEQUE', 'PC_MASTER', 'SCOTIA_MASTER', 'VISA']
    return (
      <div style={{ paddingTop: '10px' }}>
        <ExpansionPanel expanded={true}>
          <ExpansionPanelSummary>
            <Grid container spacing={10}>
              <Grid item xs={6} sm={4}>
                <Select value={this.state.selectedSource} onChange={this.onSelectType}>
                  {choices.map((c) => {
                    return (
                      <MenuItem key={c} value={c}>
                        {c}
                      </MenuItem>
                    )
                  })}
                </Select>
              </Grid>
              <Grid item xs={6} sm={4}>
                <FileUploadButton submitFileContent={this.submitFileContent} />
              </Grid>
            </Grid>
          </ExpansionPanelSummary>
          <Divider />
          <ExpansionPanelActions>
            <Button
              size="small"
              onClick={() => {
                this.props.onClose()
              }}
            >
              Cancel
            </Button>
          </ExpansionPanelActions>
        </ExpansionPanel>
      </div>
    )
  }
}
